.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #2b0050;
  background-image: linear-gradient(0deg, #2b0050 0%, #e8dbff 100%);
}

.calc {
  display: flex;
  flex-direction: column;
  
  justify-content: center;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: rgba(2, 2, 4, 0.50) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(0, 0, 0, 0.35) 0px -2px 6px 0px inset;
  width: 80vh;
  height: 70%;
  border-radius: 20px;
}
.title{
  margin-left: 510px;
  margin-bottom: -30px;
  font-size: 3rem;
  border-bottom: #444;

}
#input {
  font-size: 30px;
  color: rgb(0, 0, 0);
  text-align: center;
  margin-bottom: 30px;
  height: 10%;
  font-size: 4rem;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;

 border-radius: 50%;
 margin-bottom: 10px;
 border-radius: 50%;
 font-size: 50px;
 font-weight: 100;
}

.button {
  background-color: #444;
  color: white;
  font-size: 100px;
  border: none;
  border-radius: 50%;
  padding: 20px;
  width: 100%;
  height: 80px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  width:auto;
  height: 65px;
  font-size: 30px;
}

.button:hover {
  background-color: #555;
}

.operator-button {
  background-color: #e4a7ec;
  margin-bottom: 10px;
  width:auto;
  height: 65px;
  font-size: 30px;
}
.normal-button{
  background-color: #aacee6;
  margin-bottom: 10px;
  width:auto;
  height: 65px;
  font-size: 30px;
  font-size: 40px;
}

.operator-button:hover {
  background-color: #f5daf9;
  border:4px solid rgb(119, 0, 119);
}

.normal-button:hover {
  background-color: #b4efef;
  border:4px solid rgb(8, 8, 21);
}
.zero-button:hover {
  background-color: #b4efef;
  border:4px solid rgb(4, 55, 0);
  font-size: large;
}


.zero-button {
  grid-column: span 2;
  background-color: rgb(187, 249, 228);
  font-size: 40px;
  /* margin-top: 10px; */
  
}

@media (max-width: 480px) {
  .container {
    height: 70vh;
    padding: 20px;
    
  }

  .calc {
    width: 100%;
    height: 70vh;
    height: auto;
    padding: 10px;
    margin-top: 80px;
  }

  .buttons {
    grid-template-columns: repeat(4, 1fr);
    gap: 5px;
  }

  #input {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }

  .title {
    margin-left: 0;
    margin-bottom: 10px;
    font-size: 2rem;
    text-align: center;
  }

  .button {
    font-size: 15px;
    padding: 10px;
    height: 20px;
  }

  .operator-button,
  .normal-button,
  .zero-button {
    font-size: 20px;
    height: 30px;
  }

  .button:hover,
  .operator-button:hover,
  .normal-button:hover,
  .zero-button:hover {
    font-size: 23px;
  }
}
